/* navbar.css */

@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400..800&display=swap');

.nav {
  background-color: #1E0600;
  position: fixed;
  height: 5rem;
  width: 100vw;
  color: #F0EFF4;
  display: flex;
  justify-content: space-between;
  align-items: center; /* Center items vertically */
  font-family: "Sen", sans-serif;
  gap: 3rem;
  padding: 0 3rem;
  font-size: 1.25rem;
  text-transform: uppercase;
  z-index: 1500;
  box-sizing: border-box;
  left: 0;
  top: 0;
}

.nav a {
  color: inherit;
  text-decoration: none;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 4rem;
  align-items: center; /* Ensure ul items are centered */
  height: 100%; /* Make sure ul spans the full height of the navbar */
}

.nav li {
  display: flex;
  align-items: center; /* Center text vertically in list items */
  height: 100%;
}

.nav li:hover {
  color: #FF3864; /* Color change on hover */
  transition: color 0.3s ease;
}



/* Media Queries for Responsive Design */

@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .nav {
    font-size: 1rem; 
  }
  .nav .charlogo {
    width: auto; /* Adjust width as needed */
    max-height: 100%;
    width: 35%;
    height: auto;
  }
}

@media only screen and (min-width: 768px) and (max-width: 992px) {
  .nav {
    font-size: 0.75rem; 
  }
  .nav .charlogo {
    width: auto; /* Adjust width as needed */
    max-height: 100%;
    width: 25%;
    height: auto;
  }
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column; /* Stack items vertically */
    height: auto; /* Allow height to adjust */
    padding: 1rem; /* Add padding for spacing */
  }

  .nav ul {
    flex-direction: column; /* Stack list items vertically */
    gap: 1rem; /* Adjust gap between items */
    display: none; /* Hide the list items by default */
    width: 100%; /* Ensure full width for the list */
  }

  .nav ul.active {
    display: flex; /* Show the list items when active */
  }

  .nav .menu-toggle {
    display: flex; /* Show the menu toggle button */
    flex-direction: column;
    cursor: pointer;
  }

  .nav .menu-toggle .bar {
    display: block;
    background: white;
    height: 3px;
    width: 25px;
    margin: 5px 0;
    transition: 0.3s;
  }

  .nav .menu-toggle.active .bar1 {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .nav .menu-toggle.active .bar2 {
    opacity: 0;
  }

  .nav .menu-toggle.active .bar3 {
    transform: rotate(45deg) translate(-5px, -6px);
  }
}