@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400..800&display=swap');

.footer {
    background-color: #1E0600;
    color: #F0EFF4;
    font-family: "Sen", sans-serif;
    display: flex;
    flex-direction: row; /* Arrange items in a row */
    align-items: center;
    padding: 2rem; /* Add some padding for spacing */
}

.footer h1 {
    font-size: 3rem; /* Make the h1 text large */
    margin-right: 2rem; /* Add space between h1 and the column */
    flex-shrink: 0; /* Prevent h1 from shrinking */
    text-align: left;
}

.footer .contact-column {
    display: flex;
    flex-direction: column; /* Arrange h2 and link vertically */
    gap: 1rem; /* Add space between h2 and link */
}

.footer h2 {
    font-size: 2rem; /* Adjust font size for h2 */
    font-weight: lighter;
    padding-left: 2rem;
    margin: 0;
}

.linkedinlink h3 {
    font-size: 1.25rem;
    margin: 0;
    color: #F0EFF4; /* Keep link color consistent */
    text-decoration: none;
    transition: color 0.3s;
    font-weight: lighter;
    padding-left: 2rem;
}

.linkedinlink :hover {
    color: #FF3864; /* Add a hover effect */
}

/* Responsive styles */

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    .footer {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Align items to the start */
        padding: 1rem; /* Reduce padding for smaller screens */
        text-align: center; /* Center-align text */
    }

    .footer h1 {
        margin: 0 0 1rem 0; /* Add spacing below h1 */
        text-align: center; /* Center-align h1 */
        font-size: 1.5rem; /* Adjust font size */
    }

    .footer h2 {
        font-size: 1rem;
        text-align: center;
    }

    .footer .contact-column {
        gap: 0.5rem; /* Reduce spacing between items */
        align-items: center; /* Center-align contact column */
    }

    .linkedinlink h3 {
        font-size: 0.75rem;
        padding-left: 0; /* Remove left padding */
        text-align: center; /* Center-align text */
    }
}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .footer {
        flex-direction: column; /* Stack items vertically */
        align-items: flex-start; /* Align items to the start */
        padding: 1.5rem; /* Slightly larger padding than mobile */
    }

    .footer h1 {
        font-size: 2rem; /* Slightly smaller font size */
        margin-bottom: 1.5rem; /* Add spacing below h1 */
    }

    .footer h2 {
        font-size: 1.25rem;
        text-align: left;
        padding-left: 0;
    }

    .footer .contact-column {
        align-items: flex-start; /* Align items to the start */
    }

    .linkedinlink h3 {
        text-align: left; /* Keep text left-aligned */
        font-size: 0.75rem;
        padding-left: 0; /* Add some padding for spacing */
    }
}

/* Medium (Laptop): Min-width: 992px, Max-width: 1199px */
@media (min-width: 992px) and (max-width: 1199px) {
    .footer {
        flex-direction: row; /* Keep items in a row */
        align-items: center; /* Align items */
        justify-content: space-between; /* Add spacing between items */
    }

    .footer h1 {
        font-size: 3rem; /* Keep original size */
        margin-right: 1.5rem; /* Slightly reduce margin */
    }

    .footer .contact-column {
        gap: 1rem; /* Maintain spacing between items */
    }

    .footer h2, .linkedinlink h3 {
        padding-left: 1.5rem; /* Adjust padding for medium screens */
    }
}

/* Large (Large Desktop): Min-width: 1200px */
@media (min-width: 1200px) {
    .footer {
        flex-direction: row; /* Keep items in a row */
        align-items: center; /* Align items */
        padding: 2rem; /* Maintain original padding */
    }

    .footer h1 {
        font-size: 3rem; /* Maintain original size */
        margin-right: 2rem; /* Maintain original spacing */
    }

    .footer .contact-column {
        gap: 1rem; /* Maintain spacing */
    }
}