.figjamcluster img {
    padding-bottom: 2rem;
    width: 35%;
}

.dataflex1 {
    display: flex;
}

.dataflex1 h2 {
    width: 45%;
    margin-top: 9rem;
}

.dataflex1 img {
    padding-top: 7rem;
    padding-bottom: 2rem;
    margin-left: 6rem;
    width: 30%;
}

.dataflex2 {
    display: flex;
}

.dataflex2 h2 {
    width: 45%;
    margin-top: 7rem;
}

.dataflex2 img {
    padding-top: 5rem;
    padding-bottom: 2rem;
    margin-left: 6rem;
    width: 30%;
}

.body .grocerytravelchart {
    width: 50%;
    padding-bottom: 2rem;
}

.ScatterplotCluster {
    display: flex;
    flex-wrap: wrap; /* Allows wrapping for responsiveness */
    gap: 4rem; /* Space between items */
    justify-content: center; /* Centers content horizontally */
    align-items: flex-start; /* Aligns content to the top */
    margin-top: 30px; /* Adds spacing above the cluster */
}

.ScatterplotCluster .image-text-pair {
    display: flex;
    flex-direction: column; /* Stack image and text vertically */
    align-items: center; /* Center-align content */
    max-width: 250px; /* Optional: Limit the width of each pair */
    text-align: center; /* Center-align the text */
}

.image-text-pair .scatterplot-img-1 {
    width: 100%; /* Adjust size of the first image */
}

.image-text-pair .scatterplot-img-2 {
    width: 200%; /* Adjust size of the second image */
    margin-top: 1rem;
}

.image-text-pair .scatterplot-img-3 {
    width: 45%; /* Adjust size of the third image */
}

.image-text-pair h3 {
    font-size: 0.5rem; /* Adjust text size */
    color: #333; /* Adjust text color as needed */
    line-height: 1.5; /* Improves readability */
    margin: 0; /* Remove default margin */
}

.StoreTypeCluster {
    display: flex;
    flex-wrap: wrap; /* Allows wrapping for responsiveness */
    justify-content: center; /* Centers content horizontally */
    align-items: flex-start; /* Aligns content to the top */
    margin-top: 30px; /* Adds spacing above the cluster */
}

.StoreTypeCluster img {
    height: 300px; /* Set a consistent height */
    width: auto; /* Maintain aspect ratio */
    margin: 1rem; /* Add spacing around each image */
    object-fit: contain; /* Ensure the entire image fits within the height */
    padding-bottom: 0;
}

/* Media queries for responsiveness */

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    
    .headers h2 {
        font-size: 1rem;
        font-weight: bold;
    }
    
    .headers h1 {
        color: #FF3864;
        font-size: 1.75rem;
    }

    .dataflex1, .dataflex2 {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center-align the text and images */
        text-align: center; /* Ensure text is also centered */
    }

    .dataflex1 h2, .dataflex2 h2 {
        width: 100%; /* Full width for text */
        text-align: center; /* Center-align text */
        margin-top: 2rem; /* Adjust margin for better spacing */
    }

    .dataflex1 img, .dataflex2 img {
        margin-left: 0; /* Remove left margin */
        width: auto; /* Maintain original width settings */
        max-width: 100%; /* Ensure images fit the container */
        padding-top: 1rem; /* Add spacing above the image */
        padding-bottom: 0;
        display: block; /* Ensure images are block-level for centering */
    }

    .body h2 {
        font-size: 0.75rem;
        line-height: 1.75rem;
        padding-left: 0;
        padding-right: 0;
    }

    .body h3 {
        font-size: 0.35rem;
        line-height: 1rem;
        text-align: center;
    }

    .body .grocerytravelchart {
        width: 100%;
    }

    .body .figjamcluster img {
        width: 75%;
    }

    .StoreTypeCluster img {
        max-width: 100%;
        margin: 0;
        padding: 1rem;
        height: auto;
    }

}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .dataflex1, .dataflex2 {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center-align the text and images */
        text-align: center; /* Ensure text is also centered */
    }

    .dataflex1 h2, .dataflex2 h2 {
        width: 100%; /* Keep original width for text */
        text-align: left; /* Keep text left-aligned */
    }

    .dataflex1 img, .dataflex2 img {
        margin-left: 0; /* Remove left margin */
        width: auto; /* Maintain original width settings */
        max-width: 100%; /* Ensure images fit the container */
        padding-top: 1rem; /* Add spacing above the image */
        padding-bottom: 0;
        display: block; /* Ensure images are block-level for centering */
    }

    .StoreTypeCluster img {
        max-width: 100%;
        margin: 0;
        padding: 1rem;
    }

    .body h2 {
        font-size: 0.75rem;
    }

    .body .clockchart, .body .foodtravelchart {
        max-width: 50%;
        margin-right: auto;
        margin-left: auto;
        height: auto;
    }
}

/* Medium (Laptop): Min-width: 992px, Max-width: 1199px */
@media (min-width: 992px) and (max-width: 1199px) {
    .dataflex1, .dataflex2 {
        flex-direction: row; /* Keep items in a row */
        justify-content: space-between; /* Adjust spacing */
        align-items: center; /* Align items */
    }

    .dataflex1 h2, .dataflex2 h2 {
        width: 45%; /* Maintain text width */
        margin-top: 7rem; /* Use medium spacing */
    }

    .dataflex1 img, .dataflex2 img {
        margin-left: 4rem; /* Adjust spacing between text and image */
    }

}

/* Large (Large Desktop): Min-width: 1200px */
@media (min-width: 1200px) {
    .dataflex1, .dataflex2 {
        flex-direction: row; /* Keep items in a row */
        justify-content: space-between; /* Space out elements */
        align-items: flex-start; /* Align items to the top */
    }

    .dataflex1 h2, .dataflex2 h2 {
        width: 45%; /* Maintain text width */
        margin-top: 9rem; /* Maintain original spacing */
    }

    .dataflex1 img, .dataflex2 img {
        margin-left: 6rem; /* Use original margin */
        width: 30%; /* Maintain original size */
    }
}


