.fishCluster {
    display: flex; /* Arrange children in a row */
    justify-content: space-around; /* Add spacing between items */
    align-items: flex-start; /* Align items to the top */
}

.body .fishimg {
    width: 35%;
    padding-bottom: 0;
}

.body .fishimg2 {
    width: 35%;
    padding-bottom: 0;
}

.image-text-pair {
    display: flex; /* Arrange content in a column */
    flex-direction: column;
    align-items: center; /* Center-align text and images */
    max-width: 30%; /* Adjust the width for each image-text pair */
}

.image-text-pair img {
    width: 100%; /* Make the image responsive */
    max-width: 300px; /* Set a maximum width for the images */
    height: auto; /* Maintain aspect ratio */
    padding-bottom: 2rem;
}

.image-text-pair .eelimg {
    width: 100%; /* Make the image responsive */
    max-width: 800px; /* Set a maximum width for the images */
    height: auto; /* Maintain aspect ratio */
    padding-bottom: 2rem;
}

.image-text-pair h3 {
    text-align: center; /* Center the text below the images */
    font-size: 0.5rem; /* Adjust font size for better readability */
    width: 60%;
}

.fishgifs {
    display: flex;
    width: 100%;
}

.fishgifs img {
    width: 100%;
    max-width: 500px;
    padding-bottom: 0;
}

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    .body h3 {
        font-size: 0.35rem;
        line-height: 1rem;
        padding-bottom: 1rem;
    }

    .fishgifs {
        display: flex;
        flex-direction: column;
    }

    .fishgifs img {
        margin: 1rem;
    }

    .body .fishimg, .body .fishimg2 {
        width: 100%;
    }

    .fishCluster {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center-align items */
    }

    .fishCluster img {
        padding-bottom: 0.5rem;
    }

    .image-text-pair {
        max-width: 90%; /* Allow the text-pair to span more width */
    }

    .image-text-pair h3 {
        font-size: 0.4rem; /* Adjust font size for smaller screens */
        width: 80%; /* Adjust text width */
    }
}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .fishgifs {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .fishgifs img {
        margin: 1rem;
    }


    .fishCluster {
        flex-wrap: wrap; /* Allow items to wrap */
        justify-content: center; /* Center items horizontally */
    }

    .fishCluster img {
        padding-bottom: 0.75rem;
    }

    .image-text-pair {
        max-width: 45%; /* Adjust width for a better layout on tablets */
    }

    .image-text-pair h3 {
        font-size: 0.5rem; /* Slightly larger text for readability */
        padding-bottom: 2rem;
    }
}

/* Medium (Laptop): Min-width: 992px, Max-width: 1199px */
@media (min-width: 992px) and (max-width: 1199px) {
    .fishgifs img {
        margin-left: auto;
        margin-right: auto;

        padding-bottom: 1rem;
    }

    .fishgifs {
        flex-wrap: wrap;
        align-items: center;
    }

    .fishCluster {
        justify-content: space-evenly; /* Spread items evenly */
        align-items: center; /* Center-align items */
    }

    .fishCluster img {
        margin-top: 4rem;
        margin-left: 4rem;
        margin-right: 4rem;
    }

    .image-text-pair {
        max-width: 30%; /* Maintain consistent width */
    }

    .image-text-pair h3 {
        font-size: 0.5rem; /* Retain readability */
    }
}

/* Large (Large Desktop): Min-width: 1200px */
@media (min-width: 1200px) {
    .fishgifs {
        flex-direction: row; /* Ensure items are in a row */
        justify-content: center;
    }

    .fishCluster {
        justify-content: space-around; /* Maintain space-around layout */
        align-items: flex-start; /* Align items to the top */
    }

    .image-text-pair {
        max-width: 30%; /* Maintain original size */
    }
}