@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400..800&display=swap');

.ProjectPage {
    font-family: 'Sen', sans-serif;
    padding: 5rem;
    background-color: #F0EFF4;
    color: #271814;
}

.headers {
    padding-top: 2rem;
    text-align: center;
    font-weight: bold;
}

.headers h2 {
    font-weight: bold;
}

.headers h1 {
    color: #FF3864;
    font-size: 2.5rem;
}

.body h2 {
    font-weight: lighter;
    line-height: 3rem;
    padding: 2rem;
    font-size: 1rem;
}

.body img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 8rem;
    width: 20%;
}

.BlueTrainFlexContainer {
    display: grid;
    grid-template-columns: 1fr auto auto; /* Three columns: one for text, two for images */
    grid-template-rows: auto auto; /* Two rows: one for the main content, one for the h3 text */
    align-items: start;

}

.BlueTrainFlexContainer h2 {
    grid-column: 1 / 2; /* Place in the first column */
    grid-row: 1 / 2; /* First row for the main content */
    margin: 0;
    padding-right: 10px;
}

.body .BlueTrainFlexContainer img {
    grid-row: 1 / 2; /* First row for the main content */
    display: block;
    width: 45%;
    height: auto;
}

.BlueTrainFlexContainer img:first-of-type {
    grid-column: 2 / 3; /* Place the first image in the second column */
    padding-right: 5px;
}

.BlueTrainFlexContainer img:last-of-type {
    grid-column: 3 / 4; /* Place the second image in the third column */
    margin-left: 0;
}

.BlueTrainFlexContainer h3 {
    grid-row: 2 / 3; /* Second row for the h3 text */
    text-align: center;
    font-size: 0.5rem;
    font-weight: bold;
    margin: 0;
    line-height: 1rem;
    margin-top: -100px;
}

.BlueTrainFlexContainer h3:first-of-type {
    grid-column: 2 / 3; /* Place the first h3 in the second column */
}

.BlueTrainFlexContainer h3:last-of-type {
    grid-column: 3 / 4; /* Place the second h3 in the third column */
    width: 55%;
    margin-left: -1rem;
}

.projectLink {
    text-align: center;
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.projectLink a {
    color: #271814; /* Replace with your desired color */
    text-decoration: underline; /* Removes underline */
    font-size: 1.5rem;
    font-weight: bold; /* Optional: adds emphasis */
}


/* Adjust responsiveness */
@media (max-width: 768px) {
    .BlueTrainFlexContainer {
        grid-template-columns: 1fr; /* Stack items in one column */
        grid-template-rows: auto auto auto; /* Adjust rows for stacking */
    }

    .BlueTrainFlexContainer h2 {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        text-align: center;
        padding-right: 0;
        margin-bottom: 20px;
    }

    .BlueTrainFlexContainer img {
        grid-column: 1 / 2;
    }

    .BlueTrainFlexContainer h3 {
        grid-column: 1 / 2;
        grid-row: 3 / 4;
    }
}

