@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400..800&display=swap');

html, body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    width: 100%;
    list-style: none;
}

.Home img {
    width: 100vw;
    height: auto;
}

.About {
    width: 100vw;
    height: 100vh;
    background-color: #F0EFF4;
    font-family: "Sen", sans-serif;
    color: #1E0600;
    display: flex;
    justify-content: center;
}

.About h1 {
    font-weight: lighter;
    font-size: 5.5rem;
    margin-left: 2rem;
    letter-spacing: 0.5rem;
    color:#FF3864;
}

.About h2 {
    font-weight: lighter;
    font-size: 1.8rem;
    text-align: center;
    line-height: 4rem;
    width: 75%; /* Optional: Control how wide the text block should be */
    margin: 0 auto; /* Centers the h2 horizontally */
    padding: 0.75rem;
}

.Projects {
    background-color: #F0EFF4;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center the grid within the container */
    padding: 2rem;
    font-family: "Sen", sans-serif;
    color: #FF3864;
}

.Projects h1 {
    padding-top: 3rem;
    font-weight: lighter;
    font-size: 5.5rem;
    letter-spacing: 0.5rem;
    padding-bottom: 3rem;
}

.ProjectIcons {
    display: grid; /* Use CSS Grid for layout */
    grid-template-columns: repeat(3, 1fr); /* 3 columns in the first row */
    grid-auto-rows: auto; /* Adjust height automatically for each row */
    gap: 2rem; /* Add spacing between grid items */
    width: 70%; /* Center the grid and adjust its width */
    justify-items: center; /* Center each grid item */
    margin-left: -5rem;
    gap: 4rem 6rem;
}

.ProjectIcons .projectWrapper {
    display: flex;
    flex-direction: column; /* Stack icon and text vertically */
    align-items: center; /* Center-align text and icon */
    text-align: center; /* Center the text under each icon */
    transition: transform 0.3s ease, box-shadow 0.3s ease, border-radius 0.3s ease; /* Smooth transition for hover effects */
}

.ProjectIcons img {
    width: 20vw; /* Adjust size of icons */
    height: auto;
    margin-bottom: 1rem; /* Add spacing between icon and text */
    cursor: pointer; /* Indicate that the icon is clickable */
    border-radius: 30px;
    transition: box-shadow 0.3s ease, transform 0.3s ease, border-radius 0.3s ease; /* Smooth transition for hover effects */
}

.ProjectIcons h2, .ProjectIcons h3 {
    margin: 0; /* Remove default margins */
    color:#1E0600;
    transition: color 0.3s ease; /* Smooth transition for color change */
}

.ProjectIcons .projectWrapper:hover img {
    box-shadow: 10px 10px #FF3864; /* Add a shadow under the icon */
    transform: translateY(-5px); /* Slightly lift the icon on hover */
    transform: translateX(-5px); /* Slightly lift the icon on hover */
    border-radius: 30px;
}

.ProjectIcons .projectWrapper:hover h2,
.ProjectIcons .projectWrapper:hover h3 {
    color: #FF3864;
}

.ProjectIcons h2 {
    font-size: 1.8rem;
    margin-top: 0.5rem; /* Add slight spacing from icon */
    margin-bottom: 0.8rem;
}

.ProjectIcons h3 {
    font-size: 1.2rem;
    font-weight: lighter;
    line-height: 1.5rem;
}

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    .Home img {
        padding-top: 3rem;
    }

    .About {
        height: 45vh;
    }

    .About h1 {
        font-size: 2rem;
        text-align: center;
        margin-left: 0;
    }

    .About h2 {
        font-size: 0.75rem;
        line-height: 2rem;
        width: 90%;
        padding: 0.5rem;
    }

    .Projects h1 {
        font-size: 2rem;
    }

    .ProjectIcons {
        display: flex; /* Use flexbox */
        flex-direction: column; /* Stack items vertically */
        justify-content: center;
        align-items: center;
        gap: 2rem; /* Add spacing between items */
        width: 100%;
        margin: 0 auto;
    }

    .ProjectIcons img {
        width: 60%;
    }

    .ProjectIcons h2 {
        font-size: 1.5rem;
        text-align: center;
    }

    .ProjectIcons h3 {
        font-size: 1rem;
        text-align: center;
    }
}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .Home img {
        padding-top: 3rem;
    }

    .About {
        height: 45vh;
    }

    .About h1 {
        font-size: 3rem;
        text-align: center;
        margin-left: 0;
    }

    .About h2 {
        font-size: 1rem;
        line-height: 3rem;
        width: 80%;
        padding-bottom: 2rem;
    }

    .Projects h1 {
        font-size: 3rem;
    }

    .ProjectIcons {
        display: flex; /* Use flexbox */
        flex-direction: column; /* Stack items vertically */
        justify-content: center;
        align-items: center;
        gap: 2rem; /* Add spacing between items */
        width: 100%;
        margin: 0 auto;
    }

    .ProjectIcons img {
        width: 50%;
    }

    .ProjectIcons h2 {
        font-size: 1.6rem;
    }

    .ProjectIcons h3 {
        font-size: 1.1rem;
    }
}

/* Medium (Laptop): Min-width: 992px, Max-width: 1199px */
@media (min-width: 992px) and (max-width: 1199px) {


    .About {
        height: 85vh;
    }

    .About h1 {
        font-size: 5rem;
    }

    .About h2 {
        font-size: 1.7rem;
        line-height: 3.5rem;
        width: 75%;
    }

    .Projects h1 {
        font-size: 5.5rem;
    }

    .ProjectIcons {
        display: flex; /* Use flexbox */
        flex-direction: column; /* Stack items vertically */
        justify-content: center;
        align-items: center;
        gap: 2.5rem; /* Add spacing between items */
        width: 100%;
        margin: 0 auto;
    }

    .ProjectIcons img {
        width: 85%;
    }

    .ProjectIcons h2 {
        font-size: 1.7rem;
    }

    .ProjectIcons h3 {
        font-size: 1.2rem;
    }
}

/* Large (Large Desktop): Min-width: 1200px */
@media (min-width: 1200px) {

    .About h1 {
        font-size: 5.5rem;
    }

    .About h2 {
        font-size: 1.8rem;
        line-height: 4rem;
        width: 70%;
    }

    
}
