
.body .restaurantimg {
    width: 40%;
    padding: 0 0;
}

.websitecluster {
    display: flex; /* Arrange children in a row */
    justify-content: space-around; /* Add spacing between items */
    align-items: center; /* Center items vertically */
    margin: 20px 0; /* Add spacing around the section */
    gap: 20px; /* Add consistent spacing between the items */
}

.image-text-pair {
    display: flex; /* Arrange content in a column */
    flex-direction: column;
    align-items: center; /* Center-align text and images */
    max-width: 45%; /* Ensure images don't stretch too large */
}

.websitecluster .image-text-pair img {
    width: 100%; /* Make the image responsive */
    max-width: 2000px !important;
    height: auto; /* Maintain aspect ratio */
    padding: 0 0;
}

.body .masgif {
    width: 40%;
    padding-bottom: 0;
}

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    .body h3 {
        padding-top: 0.25rem;
        padding-bottom: 0.5rem;
        font-size: 0.4rem; /* Adjust font size for smaller screens */
    }

    .body .restaurantimg,
    .body .masgif {
        width: 80%; /* Adjust image size for smaller screens */
        margin: 0 auto; /* Center the images */
    }

    .websitecluster {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center items */
        gap: 10px; /* Reduce spacing between items */
    }

    .image-text-pair {
        max-width: 90%; /* Allow the image-text pairs to span more width */
    }
}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .body h3 {
        padding-top: 2rem;
        font-size: 0.45rem; /* Slightly larger text for tablets */
    }

    .body h2 {
        text-align: center;
    }

    .body .restaurantimg,
    .body .masgif {
        width: 60%; /* Adjust size for tablets */
        margin: 0 auto; /* Center the images */
    }

    .websitecluster {
        flex-wrap: wrap; /* Allow items to wrap */
        justify-content: center; /* Center items */
        gap: 15px; /* Adjust spacing between items */
    }

    .image-text-pair {
        max-width: 70%; /* Adjust width for better layout on tablets */
    }
}

/* Medium (Laptop): Min-width: 992px, Max-width: 1199px */
@media (min-width: 992px) and (max-width: 1199px) {
    .body h3 {
        padding-top: 2rem;
        font-size: 0.5rem; /* Keep original size */
    }

    .body .restaurantimg,
    .body .masgif {
        width: 50%; /* Adjust width for laptops */
    }

    .websitecluster {
        flex-wrap: wrap; /* Maintain flexibility */
        justify-content: space-between; /* Space items evenly */
        gap: 20px; /* Maintain consistent spacing */
    }

    .image-text-pair {
        max-width: 60%; /* Maintain reasonable size */
    }
}

/* Large (Large Desktop): Min-width: 1200px */
@media (min-width: 1200px) {
    .body h3 {
        padding-top: 2rem;
        font-size: 0.5rem; /* Keep original size */
    }

    .body .restaurantimg,
    .body .masgif {
        width: 40%; /* Keep original size */
    }

    .websitecluster {
        justify-content: space-around; /* Maintain original layout */
        gap: 20px; /* Maintain spacing */
    }

    .image-text-pair {
        max-width: 45%; /* Maintain original size */
    }
}