.body h3 {
    font-size: 0.5rem;
    text-align: center;
}

.body .gympic {
    width: 100%;
    max-width: 500px;
    padding-bottom: 10px;
}

.bowlposters {
    display: flex;
    justify-content: center;
    align-items: flex-start; 
}

.bowlposters img {
    margin: 4rem;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 10px;
}

/* Extra Small (Mobile): Max-width: 767px */
@media (max-width: 767px) {
    .bowlposters img {
        width: 45%;
        margin-left: 2rem;
        margin-right: 2rem;
    }
}

/* Small (Tablet): Min-width: 768px, Max-width: 991px */
@media (min-width: 768px) and (max-width: 991px) {
    .bowlposters img {
        width: 45%;
    }
}

